import React, { Component } from 'react';
import ReactGA from 'react-ga';
import { BrowserRouter, Route } from "react-router-dom";
import $ from 'jquery';
import './App.css';
import Header from './Components/Header';
import Footer from './Components/Footer';
import About from './Components/About';
import Resume from './Components/Resume';
import Ending from './Components/Ending';
import Contact from './Components/Contact';
import Testimonials from './Components/Testimonials';
import Portfolio from './Components/Portfolio';
import Hobby from './Components/Hobby';
import ListPage from './Components/jsnes/ListPage';
import RunPage from './Components/jsnes/RunPage';
import Config from './Components/jsnes/config';
import ReactDOM from "react-dom";


class App extends Component {

  constructor(props){
    super(props);
    this.state = {
      foo: 'bar',
      resumeData: {}
    };

    ReactGA.initialize('UA-110570651-1');
    ReactGA.pageview(window.location.pathname);

  }

  getResumeData(){
    $.ajax({
      url:'/Json/data.json',
      dataType:'json',
      cache: false,
      success: function(data){
        this.setState({resumeData: data});
      }.bind(this),
      error: function(xhr, status, err){
        console.log(err);
        alert(err);
      }
    });
  }

  getEggConfig(){
    $.ajax({
      url:'/Json/egg.json',
      dataType:'json',
      cache: false,
      success: function(data){
        this.setState({eggData: data});
      }.bind(this),
      error: function(xhr, status, err){
        console.log(err);
        alert(err);
      }
    });
  }

  componentDidMount(){
    //this.getResumeData(); 
    //this.getEggConfig();
  }

  render() {
  
        //<Route exact path="/" component={ListPage} />
        //<Route exact path="/" component={ListPage} />
        //<Route exact path="/run" component={RunPage} />
        //<Route exact path="/run/:slug" component={RunPage} />
        //<Route path="/" render={this.recordPageview} />
        // <RunPage />
        // <Hobby />
          {/* <Route path="/kegg/index.html" onEnter={reload} /> */}
        <Route path='/nestation/' component={() => {window.location.href = '/nestation/index.html'; return null;}}/>      
    return (
      <BrowserRouter>
      <div className="App">
        <Header data={this.state.resumeData.main}/>
        <About />
        <Resume />
        <Ending />
        <Footer />
      </div>
      </BrowserRouter>
    );
  }
}

export default App;



