import React, { Component } from 'react';
import RunPage from './jsnes/RunPage';

class Hobby extends Component {
  render() {
    return (
      <section id="Hobbies">
        <div className="row">
            <RunPage />
        </div>
      </section>
      
    );
  };
  
}

export default Hobby;



